import React, { Component } from 'react';
import {Section, SectionTitle} from './Section';
import SkillsIcon from 'material-ui/svg-icons/action/build';

class Skills extends Component {
  render() {
    return (
      <Section>
        <SectionTitle title="Compétences" rightIcon={<SkillsIcon/>}/>
      </Section>
    );
  }
}

export default Skills;



