import React, { Component } from 'react';
import {Section, SectionTitle} from './Section';
import ContactIcon from 'material-ui/svg-icons/content/mail';

class Contact extends Component {
  render() {
    return (
      <Section>
        <SectionTitle title="Contact" rightIcon={<ContactIcon/>}/>
      </Section>
    );
  }
}

export default Contact;



